@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,400;0,500;0,600;0,700;0,900;1,400;1,500;1,600;1,700;1,900&display=swap);
html,body {
  margin: 0;
  font-family: 'Source Code Pro', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
  color: white;
  background-color: #0A192F;
}

#root {
  height: 100%;
  width: 100%;
}
*{
  padding: 0;
  margin: 0;
}
.container {
  width: 100%;
  background-color: #0A192F;
  height: 100%;
}
header {
  width: 100%;
  padding: 10px 0;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 95%;
  margin: 0 auto;
}

.logo {}



.dropdown {
  display: none;
}

@media (max-width: 480px) {
  .logo {
    width: 50px;
  }

 

  .dropdown {
    display: block;
    color: #64FFDA;
  }
  .dropdown:hover {
    cursor: pointer;
  }
}
.list {
  list-style: none;
}

.nav-list {
  display: inline-block;
}

.nav-link {
  display: block;
  padding: 10px 15px;
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.nav-link:hover {
  cursor: pointer;
  color: #64FFDA;
}
.resp {
  display: none;
}

@media(max-width: 480px) {
  
  nav {
    display: none;
    position: absolute;
    left: 0px;
    top: 50px;
    background-color: #0A192F;
    width: 100%;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  }
  .list {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 100px;
    margin: 0 auto;
  }
  .nav-link:hover {
    cursor: pointer;
    color: #64FFDA;
  }
}
.home-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.about {
  width: 100%;
  height: 100%;
  display: flex;
}

.social {
  width: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
 
}

.aboutme {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;


}

.my-image {
 
  width: 40%;
  display: flex;
  align-items: flex-end;
}

.social-list {
  padding: 0;
  list-style: none;
}

.social-item {
  padding: 10px;
}

.social-link {
  color: white;
}

.social-link:hover {
  cursor: pointer;
  color: #64FFDA;
}

.init {
  color: #64FFDA;
  letter-spacing: 2px;
  font-size: 3rem;
}

.bold {
  font-weight: 900;
  font-size: 3rem;
  letter-spacing: 2px;
}

.more {
  margin-top: 10px;
  letter-spacing: 2px;
  font-size: 1.2rem;
 }

@media (max-width: 480px) {
  .home-container {
    flex-direction: column;
  }
  .social-item {
    display: inline-block;
  }
  .social {
    width: 80%;
    margin: 0 auto;
    order: 3;
  }
  .aboutme {
    order: 2;
    align-items: center;
    justify-content: center;
    width: 80%;
    margin: 0 auto;
    padding: 20px;
  }
  .my-image {
    width: 80%;
    margin: 0 auto;
    justify-content: center;
    order: 1;
  }
  .img {
    width: 400px;
  }
  .social {
    margin-bottom: 20px;
  }
  
}

.project {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-color: teal;
}
